	export default {
		data() {
			return {
				username: '',
        password: ''
			}
		},
    computed: {
      formReady() {
        return (
          this.username.length >= 3 &&
          this.password.length >= 6
        )
      }
    },
		methods: {
      async login() {
        console.log('login')
        await this.$http.post('authorizations', {
          username: this.username,
          password: this.password
        })
        .then(response => {
          console.log(response)
        })
        .then((error) => {
          console.log(error)
        })
      }
		}
	}
